.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.ReactVirtualized__Grid {
  user-select: none;
}

.ReactVirtualized__Grid .cell {
  border: 1px solid rgb(40, 44, 52, 0.15)
}

.ReactVirtualized__Grid .cell.selected {
  border: 1px solid rgb(97, 218, 251);
  background-color: rgba(97, 218, 251, 0.3)
}